<template>
  <div class="register-container">
    <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="用户名" prop="username">
        <el-input
          type="text"
          v-model="ruleForm.username"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input
          type="email"
          v-model="ruleForm.email"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item prop="Code">
        <el-input
          type="text"
          v-model="ruleForm.code"
          autocomplete="off"
          class="code"
        ></el-input>
        <el-button class="primary" type="primary" @click="handlerClick" plain
          >获取验证码</el-button
        >
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          v-model="ruleForm.password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="checkPass">
        <el-input
          type="password"
          v-model="ruleForm.checkPass"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="btn" type="primary" @click="submitForm()"
          >注册</el-button
        >
        <el-button class="btn" @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from '@/api/register.js';
export default {
  data() {
    var validateuser = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    let emailRule = /^[\w]+@[\w.-]+.com$/;
    var validateemail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入邮箱"));
      } else if (emailRule.test(value)) {
        callback();
      } else {
        callback(new Error("邮箱格式不正确"));
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        username: "",
        email: "",
        password: "",
        checkPass: "",
        code: "",
      },
      rules: {
        username: [{ validator: validateuser, trigger: "blur" }],
        email: [{ validator: validateemail, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm() {
      if(!this.ruleForm.code) {
        alert("请获取/填写验证码")
        return;
      }
      if(!this.ruleForm.username || !this.ruleForm.email || !this.ruleForm.password) {
        alert("信息不完整")
        return;
      }
      api.register(this.ruleForm).then((res)=>{
        if(res.status === 'success') {
          this.$router.push('/login');
        }
      })
    },
    handlerClick() {
      if(!this.ruleForm.email) {
        return alert('请输入邮箱')
      }
      api.code({email: this.ruleForm.email}).then((res)=>{
        if(res.status == 'success') {
          alert("验证码发送成功！")
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style scoped lang="less">
.register-container {
  width: 30%;
  height: 300px;
  margin: 0px auto;
  padding-top: 100px;
  .code {
    width: 50%;
    margin-right: 50px;
  }
  .primary{
    float: right;
    right: 0;
  }
  .btn{
    width: 100%;
    margin: 5px 0;
  }
}
</style>