import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@/store/index.js';
Vue.use(VueRouter);

import Login from '@/components/Home/Login.vue';
import Home from "@/components/Home/Home.vue";
import Register from '@/components/Home/Register.vue';
import Index from '@/components/Page/Index.vue';

const router = new VueRouter({
    mode: 'history',
    routes: [
        {
            name: 'login',
            path: '/login',
            component: Login,
            meta: {
                title: '登录',
                hidden: true,
                icon: ''
            },
        },
        {
            name: 'forget',
            path: '/forget',
            component: () => import('@/components/Page/Forget'),
            meta: {
                title: '忘记密码',
                hidden: true,
                icon: ''
            }
        },
        {
            name: 'modify',
            path: '/modify',
            component: () => import('@/components/Page/Modify'),
            meta: {
                title: '修改用户',
                hidden: true,
                icon: ''
            }
        },
        {
            name: 'register',
            path: '/register',
            component: Register,
            meta: {
                title: '注册',
                hidden: true,
                icon: ''
            }
        },
        {
            name: 'Index',
            path: '/',
            redirect: '/index',
            component: Home,
            meta: {
                title: '首页',
                hidden: false,
                icon: 'el-icon-s-home'
            },
            children: [{
                name: 'index',
                path: 'index',
                component: Index,
                meta: {
                    title: '统计',
                    hidden: false,
                    icon: 'el-icon-s-operation',
                    active: '4-0'
                }
            }]
        },
        {
            name: 'product',
            path: '/product',
            // redirect: '/index',
            component: Home,
            meta: {
                title: '商品',
                hidden: false,
                icon: 'el-icon-s-cooperation'
            },
            children: [{
                name: 'productList',
                path: 'list',
                component: () => import('@/components/Page/ProductList'),
                meta: {
                    title: '商品列表',
                    hidden: false,
                    icon: 'el-icon-s-unfold',
                    active: '5-0'
                }
            }, {
                name: 'productAdd',
                path: 'add',
                component: () => import('@/components/Page/ProductAdd'),
                meta: {
                    title: '添加商品',
                    hidden: false,
                    icon: 'el-icon-edit-outline',
                    active: '5-1'

                }
            }, {
                name: 'category',
                path: 'category',
                component: () => import('@/components/Page/Category'),
                meta: {
                    title: '类目管理',
                    hidden: false,
                    icon: 'el-icon-c-scale-to-original',
                    active: '5-2'
                }
            }, {
                name: 'edit',
                path: 'edit',
                component: () => import('@/components/Page/Edit'),
                meta: {
                    title: '编辑列表',
                    hidden: true,
                    icon: '',
                    active: '5-0'
                }
            }, {
                name: 'editcate',
                path: 'editcate',
                component: () => import('@/components/Page/Editcategory'),
                meta: {
                    title: '类目编辑',
                    hidden: true,
                    icon: '',
                    active: '5-2'
                }
            }]
        }
    ]
})

// 路由导航守卫
// let oneRouter = false;
router.beforeEach((to, from, next) => {
    // console.log(store.state.user)
    if (to.path !== '/login' && to.path !== '/register') {
        if (store.state.user.appkey && store.state.user.username && store.state.user.email) {
            // console.log(from)
            return next();
        } else if (to.path === '/forget' || to.path === '/modify') {
            return next()
        }
        return next('/login');
    }
    return next();
})

export default router;