<template>
  <div class="Main-Container">
    <el-row>
      <el-button
        type="success"
        class="el-icon-s-grid"
        @click="buttonClick"
      ></el-button>
    </el-row>
    <div class="breadcrumb">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <!-- <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item> -->
        <el-breadcrumb-item>{{breadcrumbOne}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{breadcrumbTwo}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <ul class="ulButton">
      <li class="user">
        欢迎：{{$store.state.user.username}}
        <i class="el-icon-arrow-down"></i>
      </li>
      <li class="userout" @click="loginOut">退出</li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isCollapse: this.$store.state.isCollapse,
      breadcrumbOne: this.$router.currentRoute.matched[0].meta.title,
      breadcrumbTwo: this.$router.currentRoute.matched[1].meta.title
    };
  },
  methods: {
    buttonClick() {
      this.$store.dispatch('changeisCollapse');
    },
    loginOut() {
      this.$store.dispatch('loginOut')
      this.$router.push({name: 'login'})
    }
  },
  watch: {
    $route() {
      this.breadcrumbOne = this.$router.currentRoute.matched[0].meta.title
      this.breadcrumbTwo = this.$router.currentRoute.matched[1].meta.title
    }
  },
};
</script>

<style scoped lang="less">
.Main-Container {
  height: 50px;
  width: 100%;
  line-height: 50px;
  border-bottom: 1px solid #eee;
  margin-bottom: 10px;
  position: relative;
  div {
    display: inline-block;
  }
  .el-button {
    width: 30px;
    height: 30px;
    padding: 0;
    margin-left: 10px;
  }
  .el-icon-s-grid {
    font-size: 20px;
  }
  .breadcrumb {
    margin-left: 20px;
  }
  ul {
    position: absolute;
    top: 0;
    right: 20px;
    z-index: 1000;
    text-align: center;
    cursor: pointer;
    list-style: none;
    margin: 0;
    padding: 0;
    li:not(:first-child) {
      display: none;
    }
    li {
      padding: 0 50px;
      &:hover {
        background: #eee;
        color: #999;
      }
    }
    &:hover {
      li {
        display: block;
      }
    }
  }
}
</style>