import Vue from 'vue';
import Vuex from 'vuex';
import { setCookie, getCookie, removeCookie} from '@/Utils/UserCookie.js';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        //导航的收缩和展开：false展开；true收缩
        isCollapse: false,
        // 设置用户信息，将其储存到cookie中
        user: getCookie(),
        editData: {},
        editnumber: {}
    },
    mutations: {
        changeisCollapse(state) {
            state.isCollapse = !state.isCollapse;
        },
        setUser(state, payload) {
            state.user = payload;
        },
        loginOut(state) {
            state.user = {
                username: '',
                appkey: '',
                role: '',
                email: ''
            }
        },
        editData(state, payload) {
            state.editData = payload
        },
        editnumber(state, payload) {
            state.editnumber = payload;
        }
    },
    actions: {
        // changeisCollapse({commit}) {
        //     commit('changeisCollapse');
        // },
        async changeisCollapse(ctx) {
            ctx.commit('changeisCollapse');
        },
        async setUserCookie(ctx, payload) {
            ctx.commit('setUser', payload);
            setCookie(payload);
        },
        async loginOut(ctx) {
            ctx.commit('loginOut');
            removeCookie();
        },
        async editData(ctx, payload) {
            ctx.commit('editData', payload)
        },
        async editnumber(ctx, payload) {
            ctx.commit('editnumber', payload)
        }
    }
})

export default store;