<template>
  <div class="NavMenu-Container">
    <el-menu
      :default-active="active"
      class="el-menu-vertical-demo"
      :class="{ 'el-menu--collapse': $store.state.isCollapse }"
      text-color="#fff"
      background-color="rgb(84, 92, 100)"
    >
      <template v-for="(item, i) in $router.options.routes">
        <el-submenu
          :index="`${i.toString()}`"
          :key="item.name"
          v-if="!item.meta.hidden"
        >
          <template slot="title">
            <i :class="item.meta.icon"></i>
            <span slot="title">{{ item.meta.title }}</span>
          </template>
          <Router-link
            :to="{ name: it.name }"
            v-for="(it, ind) in item.children"
            :key="it.name"
          >
            <template
              v-if="!($store.state.user.role == 'coustomer' && ind == 2) && !it.meta.hidden"
            >
              <el-menu-item :index="`${i.toString()}-${ind.toString()}`">
                <i :class="it.meta.icon"></i>
                {{ it.meta.title }}
              </el-menu-item>
            </template>
          </Router-link>
        </el-submenu>
      </template>
    </el-menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: this.$router.currentRoute.meta.active
    };
  },
  methods: {
  },
  watch: {
    $route() {
      this.active = this.$router.currentRoute.meta.active
    }
  },
  // created(){
  //   console.log(this.$router.options.routes)
  // }
};
</script>

<style scoped lang="less">
.NavMenu-Container {
  height: 100%;
  width: 100%;
  .el-menu-vertical-demo {
    height: 100%;
    border: none;
    transition: all 0.5s;
  }
  .el-menu--collapse {
    width: 64px;
  }
  ul li {
    ul a {
      color: #fff;
      text-decoration: none;
    }
  }
}
</style>