<template>
  <div class="login-container">
    <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="邮箱" prop="email">
        <el-input
          type="email"
          v-model="ruleForm.email"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          v-model="ruleForm.password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <div class="pass">
        <Router-link :to="{name: 'forget'}" class="forget">忘记密码</Router-link>
        <Router-link :to="{name: 'modify'}" class="modify">修改用户</Router-link>
      </div>
      <el-form-item>
        <el-button class="btn" type="primary" @click="submitForm('ruleForm')">
          登录
        </el-button>
        <el-button class="btn" @click="resetForm()">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from '@/api/login.js';
export default {
  data() {
    let emailRule = /^[\w]+@[\w.-]+.com$/;
    var validateemail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入邮箱"));
      } else if (emailRule.test(value)) {
        callback();
      } else {
        callback(new Error("邮箱格式不正确"));
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        email: "",
        password: "",
      },
      rules: {
        email: [{ validator: validateemail, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          api.login(this.ruleForm).then((res) => {
            if(res.status === 'success') {
              // console.log(res)
              this.$store.dispatch('setUserCookie', res.data)
              this.$router.push({
                name: 'Index'
              });
            }
          }).catch((error)=>{this.$message.error(error)})
          return true;
        } else {
          alert("登录失败");
          return false;
        }
      });
    },
    resetForm() {
      this.$router.push("/register");
    },
  },
  created() {
    // console.log(this.$store.state.user.email)
    if(this.$store.state.user.email) {
      this.ruleForm.email = this.$store.state.user.email;
    }
  }
};
// 邮箱： 597248157@qq.com

// 用户名： anyu

// appkey： anyu_1605098397862
</script>

<style scoped lang="less">
.login-container {
  width: 30%;
  height: 300px;
  margin: 0 auto;
  padding-top: 100px;
  .pass{
    font-size: 12px;
    height: 30px;
    .forget{
      float: left;
      margin-left: 20%;
    }
    .modify{
      float: right;
      margin-right: 20px;
    }
  }
  .btn{
    width: 100%;
    margin: 5px 0;
  }
}
</style>

