<template>
  <div id="myChart" style="width: 100%; height: 500px" ref="chart"></div>
</template>

<script>
export default {
  name: "hello",
  data() {
    return {};
  },
  mounted() {
    this.$nextTick(function () {
      this.drawLine();
    });
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let chart = this.$refs.chart;
      let myChart = this.$echarts.init(chart);
      // 绘制图表
      myChart.setOption({
        // title: { text: '在Vue中使用echarts' },
        tooltip: {},
        xAxis: {
          data: ["酒水冲调", "时令水果", "安心乳品", "休闲零食", "新鲜蔬菜"],
        },
        yAxis: {},
        series: [
          {
            name: "销量",
            type: "bar",
            data: [42, 12, 4, 1, 1],
          },
        ],
      });
    },
  },
};
</script>
